body {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    background-color: #0bb634;
    font-family: cashmarket,"Helvetica Neue",helvetica,sans-serif;
}

.main {
    height: 100vh;
}

.flex-c {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    padding: 0 24px;
}

.form-c {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.form-c form {
    text-align: center;
    margin: 0;
    width: 100%;
    margin-top: 8px;
}

.form-c h1 {
    color: #fff;
    font-size: 24px;
    font-weight: 500;
    text-align: center;
    min-height: 2.4em;
    display: flex;
    justify-content: flex-end;
    flex-direction: column;
    margin: .67em 0;
    margin-bottom: 32px;
    margin-top: 0;
}

.form-c input {
    display: block;
    border: 0;
    margin: 16px;
    text-align: center;
    outline: 0;
    border-radius: 4px;
    height: 55px;
    font-size: 18px;
    width: 100%;
}
.btn {
    text-align: center;
    font-weight: 500;
    border: none;
    padding: 10px 20px;
    border-radius: 30px;
    cursor: pointer;
    padding-top: 0;
    padding-bottom: 0;
    background-color: #15d642;
    width: 100%;
    font-size: 18px;
    height: 55px;
    color: white;
    transition: background-color .5s, color .5s;
}

.btn:hover {
    background-color: #10e642;
}

.btn:focus {
    outline: none;
}

.hide {
    text-align: center;
    font-weight: 500;
    border: none;
    padding: 10px 20px;
    border-radius: 30px;
    cursor: pointer;
    padding-top: 0;
    padding-bottom: 0;
    background-color: transparent;
    width: 100%;
    font-size: 18px;
    height: 55px;
    color: transparent;
    transition: background-color .5s, color .5s;
}

@media (max-width : 767px) {
    .form-c {
        max-width: 324px;
    }
}

@media (min-width : 480px) {
    .flex-c {
        padding: 0 40px;
    }

    .form-c {
        font-size: 20px;
    }
}

@media (min-width : 600px) {
    .form-c {
        width: 324px;
    }
}

@media (min-width : 768px) {
    .form-c {
        width: 396px;
    }
}